* {
  margin: 0;
  padding: 0;
}

nav {
  width: 100%;
  height: 60px;
  background-color: black;
}

.nav_box {
  width: 80%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  line-height: 60px;
}
.my_shop {
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
  cursor: pointer;
}

.cart span:nth-child(1) {
  font-size: 2rem;
  color: white;
}
.cart span:nth-child(2) {
  padding: 0 6px;
  background-color: rgb(211, 103, 121);
  color: white;
  border-radius: 5px;
  position: relative;
  top: -15px;
}
